/*-------------
 	General
-------------*/
*{
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

html{
	font: normal 16px sans-serif;
	color: #555;
}

nav{
	list-style: none;
}

a{
	text-decoration: none;
	color: inherit;
	cursor: pointer;

	opacity: 0.9;
}

a:hover{
	opacity: 1;
}

section{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;

	padding: 125px 100px;
}

@media (max-width: 1000px){

	section{
		padding: 100px 50px;
	}

}

@media (max-width: 600px){

	section{
		padding: 80px 30px;
	}

}

section h3.title{
	color: #414a4f;
	text-transform: capitalize;
	font: bold 32px 'Open Sans', sans-serif;
	margin-bottom: 35px;
	text-align: center;
}

.btn {
	color: #fff;
	border-radius: 4px;
	text-transform: uppercase;
	background-color: rgba(0,144,288,0.8); 
	font-weight: 800;
	text-align: center;
	border: none;
	font-size: 18px;
	font-size: 1.2rem;
}

.btn-icon {
	border-radius: 4px;
	text-transform: uppercase;
	font-weight: 800;
	text-align: center;
	border: none;
	font-size: 18px;
	font-size: 3.2rem;
	margin: 15px;
}
/*-------------
 	Header
-------------*/

header{
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	color: #756e6e;
	padding: 20px 100px 20px;
	background-color: rgba(0,144,288,0.8);
}

header h2{
	font-family: 'Quicksand', sans-serif;
}

header nav{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

header nav li{
	margin: 0 15px;
}

header nav li:first-child{
	margin-left: 0;
}

header nav li:last-child{
	margin-right: 0;
}



@media (max-width: 1000px){
	header{
		padding: 20px 50px;
	}
}


@media (max-width: 700px){
	header{
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	header h2{
		margin-bottom: 15px;
	}
}

/*----------------
 	Hero Section
----------------*/

.hero{
	position: relative;
	-ms-flex-pack: center;
	    justify-content: center;
	min-height: 100vh;
	color: #fff;
	text-align: center;
}

.hero .background-image{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-color: #2196F3;
	z-index: -1;
}

.hero .background-image:after{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #414a4f;
	opacity: 0.4444;
}

/*---------------------
 	Contact Section
---------------------*/

.contact{
	background-color: #d5e4f1;
}

.contact form{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	max-width: 800px;
	width: 80%;
}

.contact form input{
	padding: 15px;
	-ms-flex: 1 1;
	    flex: 1 1;
	font-size: 18px;
	color: #555;
	margin-bottom: 10px;
	border: #000 solid 1px;
	width: 100%;
	max-width: 400px;
}

.contact form textarea{
	padding: 15px;
	-ms-flex: 1 1;
	    flex: 1 1;
	font-size: 18px;
	color: #555;
	height: 200px;
	margin-bottom: 10px;
	border: #000 solid 1px;
	width: 100%;
	max-width: 400px;
}

.contact form button{
	padding: 18px 42px;
}


@media (max-width: 800px){

	.contact form input{
		-ms-flex-preferred-size: 100%;
		    flex-basis: 100%;
		margin: 0 0 20px 0;
	}

}



